/* Register.css */
.register-container {
  width: 100%;
  max-width: 400px;
  margin: 50px auto;
  padding: 30px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
}

h2.title {
  font-size: 28px;
  color: #333;
  margin-bottom: 20px;
}

input {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  color: #333;
  transition: border-color 0.3s ease;
}

input:focus {
  border-color: #4caf50;
  outline: none;
}

button {
  width: 100%;
  padding: 14px;
  background-color: #4caf50;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

.error-message {
  color: #d9534f;
  font-size: 14px;
}

p {
  font-size: 14px;
  margin-top: 20px;
}

a {
  color: #4caf50;
  text-decoration: none;
}
.login-link-text{
  color: darkblue;
  text-decoration: solid underline;

}
a:hover {
  text-decoration: underline;
}

/* Popup Styles */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
}

.popup input {
  width: 80%;
  padding: 10px;
  margin-bottom: 10px;
}

.popup button {
  width: 100%;
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.popup button:hover {
  background-color: #45a049;
}

.popup button:last-child {
  background-color: #f44336;
}

.popup button:last-child:hover {
  background-color: #e53935;
}
/* Add to Register.css */
.popup .error-message {
  color: #d9534f;
  font-size: 14px;
  margin-top: 10px;
}
